
import { defineComponent, ref, watch } from 'vue'
import { useRoute } from 'vue-router'
export default defineComponent({
    setup() {
        const route = useRoute()
        const isPost = ref(false as Boolean)
        watch(() => route.name, () => {
            isPost.value = route.name === 'Post'
        })

        return {
            isPost
        }
    }
})
